:root {
  --pf-chart-global--FontFamily: $font-family-base;
  --pf-global--FontSize--md: $font-size-base;
  --pf-global--FontSize--sm: 13px;
}

.kubevirt-clone-vm-modal__configuration-summary {
  overflow-y: auto;
  max-height: 450px;
}

.kubevirt-clone-vm-modal__configuration-summary dt {
  color: #72767b;
  text-transform: capitalize;
}

.kubevirt-clone-vm-modal__start_vm_checkbox>input[type="checkbox"] {
  margin: -0.25em 0 0 !important;
}

.kubevirt-clone-vm-modal__description {
  min-height: 3em;
  resize: vertical;
}

