.odc-topology {
  display: grid;
  height: 100%;
}

.odc-topology .pf-topology-container__with-sidebar .pf-topology-content {
  position: relative;
}

.odc-topology__hint-container {
  align-items: center;
  background: var(--pf-global--Color--light-100);
  border: 1px solid var(--pf-global--BorderColor--light-100);
  border-radius: 8px;
  display: flex;
  padding: var(--pf-global--spacer--xs) var(--pf-global--spacer--sm);
  pointer-events: none;
  position: absolute;
  top: var(--pf-global--spacer--sm);
}

.odc-topology__layout-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 var(--pf-global--spacer--sm);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-c-button {
  border-radius: 0;
  margin-right: 0;
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-c-button:focus {
  z-index: 10;
  color: var(--pf-global--active-color--400);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-c-button.pf-m-active {
  background-color: var(--pf-global--Color--light-300);
  color: var(--pf-global--icon--Color--dark);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-c-button:hover {
  z-index: 20;
  color: var(--pf-global--active-color--400);
}

.odc-topology__layout-button .pf-topology-control-bar__button.pf-c-button:hover.pf-m-active {
  color: var(--pf-global--icon--Color--dark);
}

.odc-topology__layout-button:first-of-type .pf-topology-control-bar__button.pf-c-button {
  border-radius: var(--pf-global--BorderRadius--sm) 0 0 var(--pf-global--BorderRadius--sm);
}

.odc-topology__layout-button:last-of-type .pf-topology-control-bar__button.pf-c-button {
  border-radius: 0 var(--pf-global--BorderRadius--sm) var(--pf-global--BorderRadius--sm) 0;
}

.odc-topology__layout-button__icon {
  margin-right: var(--pf-global--spacer--xs);
}

.odc-topology__shortcuts-button {
  margin-right: var(--pf-global--spacer--2xl);
}

.odc-topology .pf-topology-container {
  display: flex;
}

