.odc-helm-release {
  outline: none;
  cursor: pointer;
}

.odc-m-drag-active .odc-helm-release {
  pointer-events: none;
}

.odc-helm-release__bg {
  fill: #d2d2d2;
  fill-opacity: .5;
  stroke: #8a8d90;
  stroke-width: 2.5px;
  stroke-dasharray: 5 6;
}

.odc-helm-release.is-filtered .odc-helm-release__bg {
  stroke: #ef9234;
  stroke-width: 3px;
  stroke-dasharray: none;
}

.odc-helm-release.is-selected .odc-helm-release__bg {
  stroke: #06c;
  fill: #A5C4E2;
  stroke-width: 3px;
  stroke-dasharray: 5 6;
}

.odc-m-drag-active .odc-helm-release,
.odc-m-filter-active .odc-helm-release {
  opacity: .4;
}

.odc-m-drag-active .odc-helm-release.is-dragging,
.odc-m-drag-active .odc-helm-release.is-filtered,
.odc-m-filter-active .odc-helm-release.is-dragging,
.odc-m-filter-active .odc-helm-release.is-filtered {
  opacity: 1;
}

