.kubevirt-vm-node__bg {
  fill: var(--pf-global--BackgroundColor--light-100);
}

.kubevirt-vm-node__status {
  fill-opacity: 1;
  fill: var(--pf-color-purple-300);
  stroke-width: 0;
}

.kubevirt-m-running .kubevirt-vm-node__status {
  fill: var(--pf-global--primary-color--100);
}

.kubevirt-m-not-ready .kubevirt-vm-node__status {
  fill: var(--pf-chart-color-blue-200);
}

.kubevirt-m-warning .kubevirt-vm-node__status {
  fill: var(--pf-chart-color-gold-400);
}

.kubevirt-m-error .kubevirt-vm-node__status {
  fill: var(--pf-color-red-100);
}

.kubevirt-m-pending .kubevirt-vm-node__status {
  fill: var(--pf-chart-color-blue-100);
}

.kubevirt-m-success .kubevirt-vm-node__status {
  fill: var(--pf-color-green-500);
}

.kubevirt-m-terminating .kubevirt-vm-node__status {
  fill: var(--pf-chart-color-blue-600);
}

.kubevirt-m-unknown .kubevirt-vm-node__status {
  fill: var(--pf-color-purple-300);
}

.kubevirt-m-off .kubevirt-vm-node__status {
  fill: var(--pf-global--Color--light-100);
}

.kubevirt-m-importing .kubevirt-vm-node__status {
  fill: var(--pf-color-black-400);
}

.kubevirt-vm-node.kubevirt-m-off .kubevirt-vm-node__bg,
.kubevirt-vm-node.kubevirt-m-off .kubevirt-vm-node__status {
  stroke-width: 1;
  stroke: var(--pf-global--BorderColor--100);
}

