.odc-event-source {
  cursor: pointer;
}

.odc-m-drag-active .odc-event-source {
  pointer-events: none;
}

.odc-event-source__bg {
  fill: var(--pf-global--BackgroundColor--light-100);
}

.odc-event-source.is-filtered .odc-event-source__bg {
  stroke-width: 2px;
  stroke: #ef9234;
}

.odc-event-source.is-selected .odc-event-source__bg {
  stroke-width: 2px;
  stroke: #06c;
}

.odc-m-drag-active .odc-event-source,
.odc-m-filter-active .odc-event-source {
  opacity: .4;
}

.odc-m-drag-active .odc-event-source.is-dragging,
.odc-m-filter-active .odc-event-source.is-dragging {
  opacity: 1;
}

.odc-m-filter-active:not(.odc-m-drag-active) .odc-event-source.is-filtered {
  opacity: 1;
}

